.avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;

  @media (max-width: 460px) {
    width: 24px;
    height: 24px;
  }

  a {
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }
}